.card {
  max-width: 500px;
  height: max-content;
  margin: 12px 3px 25px 3px;
  border: 1px solid rgb(173, 173, 173);
  border-radius: 5px;
}

.card-header {
  display: flex;
}

.card-header > h5 {
  margin: 0;
  padding: 11px;
}

.card-pic img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  height: auto;
  object-fit: contain;
  padding: 5px;
}

.card-header-detail {
  display: block;
}
.card-header-detail p {
  margin-top: 2px;
}
.card-image img {
  width: 100%;
  height: fit-content;
}

.card-body {
  margin: 1em;
}
.card-content {
  display: flex;
  justify-content: space-around;
  text-align: left;
  line-height: 4px;
  padding: 3px 10px;
  border-bottom: 1px solid rgb(173, 173, 173);
}

.card-content button {
  border: none;
}
.card-content div {
  display: flex;
  justify-content: space-around;
}
.add-comment {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.add-comment input {
  margin: 0;
  outline: none;
  border: none;
  padding: 10px;
}

.comment {
  border: none;
  background-color: none;
  font-weight: bolder;
  cursor: pointer;
  color: #339ce3;
}

.material-symbols-outlined-red {
  color: red;
  font-variation-settings: "FILL" 1, "wght" 700, "GRAD" 0, "opsz" 48;
}
.material-symbols-outlined {
  cursor: pointer;
}

.showComment {
  width: 100vw;
  min-height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(16, 13, 13, 0.4);
}

.container {
  display: flex;
  flex-direction: column;
  width: 80%;
  background-color: white;
  position: absolute;
  top: 10%;
  left: 10%;
  height: 500px;
}

.postPic {
  background-color: black;
  display: flex;
  align-items: center;
}

.postPic img {
  object-fit: contain;
  width: 100%;
}

.details {
  width: 100%;
  height: inherit;
  display: flex;
  flex-direction: column;
}

.comment-section {
  flex-grow: 4;
  text-align: left;
  margin-left: 10px;
}

.close-comment {
  position: fixed;
  top: 1%;
  right: 5%;
  color: white;
  border: none;
  cursor: pointer;
}

.material-symbols-outlined-comment {
  font-size: 50px;
  font-variation-settings: "FILL" 0, "wght" 700, "GRAD" 0, "opsz" 48;
}

/* commit modal css */

.padding {
  padding: 0px 10px;
}

.container {
  height: 80vh;
  overflow-y: scroll;
  overflow-x: hidden;
  outline: none;
  border-radius: 18px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.header {
  display: flex;
  align-items: center;
}
.header h4 {
  width: fit-content;
  margin: auto;
  margin-top: 12px;
  margin-bottom: 7px;
}

.profile_header {
  display: flex;
  justify-content: space-between;
}

.image img {
  height: 40px;
  width: 40px;
  border-radius: 50%;
}

.profile_header p {
  margin-left: 8px;
  margin-top: 6px;
  margin-bottom: 0px;
  font-weight: 600;
}

.profile_header div {
  margin-left: 8px;
}

.post_img {
  padding: 0px 10px;
}
.post_img img {
  width: 100%;
  height: fit-content;
}

.like_section {
  display: flex;
  justify-content: space-around;
}
.like_section p {
  margin: 0px;
}

.single_commit {
  background-color: #f0f2f5;
  height: fit-content;
  width: fit-content;
  padding: 0px 12px;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 6px;
}

.single_commit p:nth-child(1) {
  margin-bottom: 4px;
  font-weight: 600;
}

.single_commit p:nth-child(2) {
  margin-top: 0px;
}
